import React, { useContext, useMemo } from "react";

import { createMuiTheme, SimplePaletteColorOptions } from "@material-ui/core/styles";
import MuiThemeProvider from "@material-ui/core/styles/MuiThemeProvider";
import { Color } from "@material-ui/core";
import { PaletteOptions } from "@material-ui/core/styles/createPalette";
import { TypographyOptions } from "@material-ui/core/styles/createTypography";
import { Shadows } from "@material-ui/core/styles/shadows";

import { BrandContext, Theme } from "../BrandContext/BrandContext";
import { ThemeOptions } from "@material-ui/core/styles/createMuiTheme";

export default function ThemeProviderCustom({ children }) {
    var ctx = useContext(BrandContext);
    if (ctx == null) throw { message: "BrandContext not found. please import BrandContextProvider in your root App." };

    const theme = useMemo(() => buildTheme(ctx.theme), [ctx.theme]);
    return <MuiThemeProvider theme={theme}>{children}</MuiThemeProvider>;
}

function buildTheme(ctxTheme: Theme) {
    return createMuiTheme({
        themeName: ctxTheme.themeName,
        breakpoints: {
            values: {
                xs: 0,
                sm: 600,
                md: 960,
                lg: 1024,
                xl: 1280
            }
        },
        palette: {
            primary: {
                lighter: ctxTheme.primaryLighter,
                light: ctxTheme.primaryLight,
                main: ctxTheme.primary,
                dark: ctxTheme.primaryDark,
                darkHover: ctxTheme.primaryDarkHover,
                alpha: ctxTheme.primaryAlpha,
                alphaDark: ctxTheme.primaryAlphaDark
            } as SimplePaletteColorOptions | Partial<Color> | undefined,
            secondary: {
                lighter: ctxTheme.secondaryLighter,
                light: ctxTheme.secondaryHover,
                main: ctxTheme.secondary,
                dark: ctxTheme.secondaryDark,
                contrastText: ctxTheme.contrastSecondaryText
            } as any,
            error: { main: ctxTheme.error },
            alert: { main: ctxTheme.alert },
            success: {
                main: ctxTheme.success,
                hover: ctxTheme.successHover
            },
            text: {
                primary: ctxTheme.primaryText,
                secondary: ctxTheme.secondaryText,
                disabled: ctxTheme.disabled
            },
            grey: {
                50: ctxTheme.border,
                1000: ctxTheme.offwhite
            } as Partial<Color>,
            tabs: {
                main: ctxTheme.tabs,
                hover: ctxTheme.tabsHover
            },
            spot: { main: ctxTheme.spot },
            hotel: { main: ctxTheme.hotel },
            air: { main: ctxTheme.air },
            package: { main: ctxTheme.packageColor },
            car: { main: ctxTheme.car },
            white: {
                main: ctxTheme.white,
                alpha: ctxTheme.white30
            },
            backdrop: {
                main: ctxTheme.backdrop,
                dark: ctxTheme.backdrop70,
                darker: ctxTheme.backdrop100
            },
            canceled: { main: ctxTheme.canceled },
            approved: { main: ctxTheme.approved },
            processed: { main: ctxTheme.processed },
            greyscale: { 1: "#EFEFEF", 3: "#CACACA" },
            gradient: {
                black: {
                    bottom: "linear-gradient(180.35deg, rgba(0, 0, 0, 0) 40.1%, rgba(0, 0, 0, 0.6) 99.49%)",
                    leftToRigth: "linear-gradient(90deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.6) 100%)"
                }
            }
        } as PaletteOptions,
        typography: {
            useNextVariants: true,
            color: ctxTheme.primaryText,
            fontFamily: ctxTheme.regular,
            fontSize: 16,
            h1: {
                fontFamily: ctxTheme.bold,
                fontSize: "48px",
                lineHeight: "60px"
            },
            h2: {
                fontFamily: ctxTheme.bold,
                fontSize: "36px",
                lineHeight: "40px"
            },
            h3: {
                fontFamily: ctxTheme.bold,
                fontSize: "26px",
                lineHeight: "30px"
            },
            h4: {
                fontFamily: ctxTheme.bold,
                fontSize: "22px",
                lineHeight: "28px"
            },
            h5: {
                fontFamily: ctxTheme.bold,
                fontSize: "20px",
                lineHeight: "26px"
            },
            h6: {
                fontFamily: ctxTheme.bold,
                fontSize: "18px",
                lineHeight: "24px"
            },
            subtitle: { fontSize: "14px", lineHeight: "18px" },
            caption: {
                color: ctxTheme.primary,
                fontFamily: ctxTheme.regular,
                fontSize: "12px",
                lineHeight: "16px"
            },
            body1: { fontSize: "16px" },
            body2: { fontSize: "14px" },
            body3: { fontSize: "12px" }
        } as TypographyOptions,
        font: {
            regular: ctxTheme.regular,
            semibold: ctxTheme.bold,
            bold: ctxTheme.bold
        },
        shadows: [
            "0px 0px 0px 0px rgba(0,0,0,0.16)",
            "0px 0px 1px 0px rgba(0,0,0,0.16)",
            "0px 0px 2px 0px rgba(0,0,0,0.16)",
            "0px 0px 3px 0px rgba(0,0,0,0.16)",
            "0px 0px 4px 0px rgba(0,0,0,0.16)",
            "0px 0px 5px 0px rgba(0,0,0,0.16)",
            "0px 0px 6px 0px rgba(0,0,0,0.16)",
            "0px 0px 7px 0px rgba(0,0,0,0.16)",
            "0px 0px 8px 0px rgba(0,0,0,0.16)",
            "0px 0px 9px 0px rgba(0,0,0,0.16)",
            "0px 0px 10px 0px rgba(0,0,0,0.16)",
            "0px 0px 11px 0px rgba(0,0,0,0.16)",
            "0px 0px 12px 0px rgba(0,0,0,0.16)",
            "0px 0px 13px 0px rgba(0,0,0,0.16)",
            "0px 0px 14px 0px rgba(0,0,0,0.16)",
            "0px 0px 15px 0px rgba(0,0,0,0.16)",
            "0px 0px 16px 0px rgba(0,0,0,0.16)",
            "0px 0px 17px 0px rgba(0,0,0,0.16)",
            "0px 0px 18px 0px rgba(0,0,0,0.16)",
            "0px 0px 19px 0px rgba(0,0,0,0.16)",
            "0px 0px 20px 0px rgba(0,0,0,0.16)",
            "0px 0px 21px 0px rgba(0,0,0,0.16)",
            "0px 0px 22px 0px rgba(0,0,0,0.16)",
            "0px 0px 23px 0px rgba(0,0,0,0.16)",
            "0px 0px 24px 0px rgba(0,0,0,0.16)"
        ] as unknown as Shadows,
        level: {
            negative: -1,
            base: 0,
            content: 9,
            modal: 99,
            fixed: 999
        },
        container: { main: "1284px" }
    } as ThemeOptions);
}
